import { IProcessType, IListable, ISmartTableColumns } from '../../interfaces';
import { Guid } from 'guid-typescript';

export class ProcessType implements IProcessType, IListable {
  id: Guid;
  name: string;

  constructor() {}

  getColumns = (): ISmartTableColumns => {
    return {
      name: {
        title: 'sim',
        width: '100%',
      },
    };
  };
  getLabelKey = () => 'name';
  getValueKey = () => 'id';
}
