export interface IFeature {
  key: string;
  description: string;
  defaultValue: string;
  featureType: FeatureType;
  featureValue: FeatureValueType;
  subFeatures: Array<IFeature>;
}

export enum FeatureType {
  VALUE,
  KEY,
  PRIMARY_KEY,
  FOREIGN_KEY,
  DISPLAY_TEXT,
}

export enum FeatureValueType {
  TEXT,
  LONG,
  DOUBLE,
  DATE,
  LIST,
  RECORD,
}

export const FeatureTypeSelect = [
  { label: '', value: FeatureType.VALUE },
  { label: '', value: FeatureType.KEY },
  { label: '', value: FeatureType.PRIMARY_KEY },
  { label: '', value: FeatureType.FOREIGN_KEY },
  { label: '', value: FeatureType.DISPLAY_TEXT },
];

export const FeatureValueTypeSelect = [
  { label: 'Text', value: FeatureValueType.TEXT },
  { label: 'Long', value: FeatureValueType.LONG },
  { label: 'Double', value: FeatureValueType.DOUBLE },
  { label: 'Date', value: FeatureValueType.DATE },
  { label: 'List', value: FeatureValueType.LIST },
  { label: 'Record', value: FeatureValueType.RECORD },
];
