import { IRecord, IListable, ISmartTableColumns } from '../../interfaces';
import { Guid } from 'guid-typescript';

export class Record implements IRecord, IListable {
  id: Guid;
  registerId: Guid;
  key: Array<string>;
  value: Array<string>;
  informationId: Guid;

  getLabelKey = () => 'key';
  getValueKey = () => 'value';
  getColumns = (): ISmartTableColumns => {
    return {
      key: {
        title: 'Anahtar',
        width: '20%',
        filter: false,
        sort: false,
        valuePrepareFunction: (value: Array<string>) => value.join(','),
      },
      value: {
        title: 'Deer',
        width: '50%',
        filter: false,
        sort: false,
        valuePrepareFunction: (value: Array<string>) => value.join(','),
      },
      informationId: {
        title: 'Bilgi',
        width: '30%',
        filter: false,
        sort: false,
        type: 'custom',
        propType: 'information',
      },
    };
  };
}
