import { Guid } from 'guid-typescript';
import { IListable, ISmartTableColumns, IStakeholder } from '../../interfaces';

export class StakeHolder implements IListable, IStakeholder {
  id: Guid;
  logoId: string;
  title?: string;
  url: string;
  active: boolean;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      title: {
        title: 'Payda Ad',
        width: '55%',
      },
      url: {
        title: 'Balant',
        width: '35%',
      },
      active: {
        title: 'Aktif',
        width: '10%',
        propType: 'boolean',
      },
    };
  }

  getLabelKey = () => 'title';
  getValueKey = () => 'id';
}
