import { Guid } from 'guid-typescript';
import {
  IListable,
  ISmartTableColumns,
  IFaaliyet,
  IFaaliyetEkFaaliyet,
  IFaaliyetMeslekKolu,
} from '../../interfaces';

export class Faaliyet implements IFaaliyet, IListable {
  id: Guid;
  kodu: string;
  tanimi: string;
  kisim: string;
  seviye: number;
  ustkodu: string;
  ekFaaliyetler: Array<IFaaliyetEkFaaliyet>;
  meslekKollari: Array<IFaaliyetMeslekKolu>;
  ruhsatlar: Array<{ ruhsatTuruId: Guid }>;
  aktif: boolean;
  ozelIzinler: Array<{ ozelIzinId: string; ozelIzinDescription }>;
  isEsnaf: boolean;
  isTacir: boolean;
  isGercekUsul: boolean;
  isBasitUsul: boolean;
  etiketler: string;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      kodu: {
        title: 'Nace Kodu',
        width: '10%',
      },
      tanimi: {
        title: 'Tanm',
        width: '80%',
      },
      aktif: {
        title: 'Aktif',
        propType: 'boolean',
        width: '10%',
      },
    };
  }

  getLabelKey = () => 'tanimi';
  getValueKey = () => 'id';
}
