import { Guid } from 'guid-typescript';
import { IListable, IInstitution, ISmartTableColumns } from '../../interfaces';

export class Institution implements IInstitution, IListable {
  id: Guid;
  code: number;
  name: string;
  description: string;
  institutionTypeId: Guid;
  departmentTypeId: Guid;
  rootInstitutionId: Guid;
  institutionalParentId: Guid;
  departmentalParentId: Guid;
  level: number;
  city: string;
  district: string;
  address: string;
  isInOperation: boolean;
  isCorrespondent: boolean;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      name: {
        title: 'sim',
        width: '20%',
      },
      description: {
        title: 'Aklama',
        width: '25%',
      },
      rootInstitutionId: {
        title: 'Ana Kurum',
        type: 'custom',
        width: '20%',
        navigationproperty: 'rootinstitution.name',
        propType: 'institution',
      },
      institutionalParentId: {
        title: 'Bal Kurum',
        type: 'custom',
        width: '20%',
        navigationproperty: 'institutionalparent.name',
        propType: 'institution',
      },
      level: {
        title: 'Seviye',
        propType: 'number',
        width: '5%',
      },
      isCorrespondent: {
        title: 'D Yazma',
        propType: 'boolean',
        width: '5%',
      },
      isInOperation: {
        title: 'Aktif',
        propType: 'boolean',
        width: '5%',
      },
    };
  }

  getLabelKey = () => 'description';
  getValueKey = () => 'id';
}
