import { Guid } from 'guid-typescript';
import { IListable, ISmartTableColumns } from '../../interfaces';

export class ErrorMessage implements IListable {
  id: Guid;
  errorName: string;
  description: string;
  message: string;
  errorFields: Array<ErrorMessageField>;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      // errorName: {
      //   title: 'Ad',
      //   width: '20%',
      // },
      description: {
        title: 'Aklama',
        width: '30%',
      },
      message: {
        title: 'Mesaj',
        width: '70%',
      },
    };
  }

  getLabelKey = () => 'errorName';
  getValueKey = () => 'id';
}

export interface ErrorMessageField {
  id: Guid;
  errorMessageId: Guid;
  fieldName: string;
}
