import { Guid } from 'guid-typescript';
import { IIsletmeturu, IListable, ISmartTableColumns } from '@psc-clients/psc-models';

export class IsletmeTuru implements IIsletmeturu, IListable {
  id: Guid;
  sicilAdi: string;
  aciklama: string;
  aktif: boolean;
  informationId: Guid;
  isSahisIsletmesi: boolean;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      sicilAdi: {
        title: 'Sicil Ad',
        width: '40%',
      },
      informationId: {
        navigationproperty: 'Information.Name',
        title: 'Bilgi',
        width: '40%',
        type: 'custom',
        propType: 'information',
      },
      isSahisIsletmesi: {
        title: 'ahs letmesi',
        propType: 'boolean',
        width: '10%',
      },
      aktif: {
        title: 'Aktif',
        propType: 'boolean',
        width: '10%',
      },
    };
  }

  getLabelKey = () => 'sicilAdi';
  getValueKey = () => 'id';
}
