import { IForm, IListable, IComponent, ISmartTableColumns } from '../../interfaces';
import { Guid } from 'guid-typescript';

export class Form implements IForm, IListable {
  id: Guid;
  name: string;
  description: string;
  showInSequence: boolean;
  components: Array<IComponent>;
  parameterKeys: Array<string>;

  constructor() {
    this.components = [];
    this.parameterKeys = [];
  }

  getColumns = (): ISmartTableColumns => {
    return {
      name: {
        title: 'sim',
        width: '40%',
      },
      description: {
        title: 'Aklama',
        width: '50%',
      },
      showInSequence: {
        title: 'Srayla Gster',
        width: '10%',
        propType: 'boolean',
      },
    };
  };

  getLabelKey = () => 'name';

  getValueKey = () => 'id';
}
