import { Guid } from 'guid-typescript';
import { IEkFaaliyet, IListable, ISmartTableColumns } from '../../interfaces';

export class EkFaaliyet implements IEkFaaliyet, IListable {
  id: Guid;
  tanimi: string;
  aciklama: string;
  faaliyetSayisi: number;
  faaliyetler: Array<{ faaliyetId: Guid; faaliyetTanimi: string }>;
  ozelIzinler: Array<{ ozelIzinDescription: string; ozelIzinId: Guid }>;
  ruhsatTurleri: Array<{ ruhsatTuruId: Guid; ruhsatTuruTanimi: string }>;
  aktif: boolean;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      tanimi: {
        title: 'Tanm',
        width: '50%',
      },
      aciklama: {
        title: 'Aklama',
        width: '40%',
      },
      aktif: {
        title: 'Aktif',
        propType: 'boolean',
        width: '10%',
      },
    };
  }

  getLabelKey = () => 'tanimi';
  getValueKey = () => 'id';
}
