import { BasvuruDurumTuru, BasvuruDurumTuruOptions } from '@psc-clients/psc-models';
import React from 'react';

export class Tamamla extends React.Component<
  { basvuruId: string },
  {
    parameters: Array<Record<string, string>>;
    statusType?: BasvuruDurumTuru;
    sendParameters: boolean;
    description: string;
  }
> {
  constructor(props) {
    super(props);
    this.state = {
      statusType: BasvuruDurumTuru.COMPLETED,
      parameters: [],
      description: '',
      sendParameters: true,
    };
  }

  updateState = (key: string, value: string) => {
    this.setState({
      ...this.state,
      [key]: value,
    });
  };

  addParameter = () => {
    this.setState({
      ...this.state,
      parameters: [
        ...this.state.parameters,
        {
          key: '',
          value: '',
        },
      ],
    });
  };

  updateParameter = (key: string, index: number, value: string) => {
    this.setState({
      ...this.state,
      parameters: [
        ...this.state.parameters.map((param, idx) =>
          idx === index ? { ...param, [key]: value } : param,
        ),
      ],
    });
  };

  removeParameterAt = (idx: number) => {
    this.setState({
      ...this.state,
      parameters: [...this.state.parameters.filter((val, index) => index !== idx)],
    });
  };

  handleSubmit = (event) => {
    //
    const { basvuruId } = this.props;
    const { parameters, sendParameters } = this.state;

    const form = document.createElement('form');
    const actionUrl = `https://perbis-test.ticaret.gov.tr/api/workflow/external/basvuru/${basvuruId}/devam`;
    form.action = actionUrl;
    form.method = 'post';
    const input = document.createElement('input');
    input.name = 'statusType';
    input.id = 'statusType';
    input.value = this.state.statusType.toString();
    form.appendChild(input);
    const descriptionInput = document.createElement('input');
    descriptionInput.name = 'description';
    descriptionInput.id = 'description';
    descriptionInput.value = this.state.statusType.toString();
    form.appendChild(descriptionInput);

    if (sendParameters) {
      parameters.forEach((param) => {
        const input = document.createElement('input');
        input.name = `params.${param.key}`;
        input.id = `params.${param.key}`;
        input.value = param.value;
        form.appendChild(input);
      });
    }

    document.body.appendChild(form);

    form.submit();

    event.preventDefault();
  };

  render() {
    const { parameters, statusType, sendParameters, description } = this.state;
    return (
      <form onSubmit={this.handleSubmit}>
        <div className="form-group">
          <label htmlFor="statusType" className="label col-form-label">
            Bavuru Durumu
          </label>
          <select name="statusType" id="statusType" className="form-control">
            <option></option>
            {BasvuruDurumTuruOptions.filter((x) => x.value !== BasvuruDurumTuru.WAITING).map(
              (option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ),
            )}
          </select>
        </div>
        <div className="form-group">
          <label htmlFor="description" className="label col-form-label">
            Bavuru Durumu Aklama
          </label>
          <input
            value={description}
            onChange={(e) => this.updateState('description', e.target.value)}
            className="form-control"
            type="text"
            name="description"
            id="description"
          />
        </div>
        <input
          type="checkbox"
          id="checkbox"
          checked={sendParameters}
          onChange={(e) => this.updateState('sendParameters', e.target.checked as any)}
        />
        <label htmlFor="checkbox">Parametre Gnder</label>
        <div className="row d-flex justify-content-end mx-2">
          <button type="button" className="btn btn-primary" onClick={() => this.addParameter()}>
            Parametre Ekle
          </button>
        </div>
        <section style={{ paddingTop: '3rem', paddingBottom: '3rem' }}>
          {parameters.map((param, index) => (
            <React.Fragment key={index}>
              <div className="row d-flex justify-content-end mx-2">
                <button
                  type="button"
                  className="btn btn-danger"
                  onClick={() => this.removeParameterAt(index)}
                >
                  Sil
                </button>
              </div>
              <div
                style={{ padding: '1rem 2rem', border: '2px solid #fafafa' }}
                className="form-group row"
              >
                <div className="col">
                  <label htmlFor="key" className="label col-form-label">
                    Parametre Ad
                  </label>
                  <input
                    type="text"
                    id="key"
                    className="form-control"
                    value={param.key}
                    onChange={(e) => this.updateParameter('key', index, e.target.value)}
                    disabled={!sendParameters}
                  />
                </div>
                <div className="col">
                  <label htmlFor="key" className="label col-form-label">
                    Parametre Deeri
                  </label>
                  <input
                    type="text"
                    id="value"
                    className="form-control"
                    value={param.value}
                    onChange={(e) => this.updateParameter('value', index, e.target.value)}
                    disabled={!sendParameters}
                  />
                </div>
              </div>
            </React.Fragment>
          ))}
        </section>
        <input
          name="statusType"
          type="hidden"
          value={statusType}
          onChange={(event) => this.updateState('statusType', event.target.value)}
        />
        <div className="d-flex justify-content-end mx-2">
          <button type="submit" className="btn btn-primary">
            Kaydet
          </button>
        </div>
      </form>
    );
  }
}
