import { Guid } from 'guid-typescript';
import {
  IDocumentType,
  IInstitution,
  IConnectorOperation,
  IListable,
  ISmartTableColumns,
} from '../../interfaces';

export class DocumentType implements IDocumentType, IListable {
  id: Guid;
  code: string;
  name: string;
  institutionId: Guid;

  connectorOperation?: IConnectorOperation;
  template?: any;
  parameters?: Array<string>;
  institution?: IInstitution;

  constructor(private actionRenderer?: any) {}

  getColumns(): ISmartTableColumns {
    const columns: ISmartTableColumns = {
      name: {
        title: 'Ad',
        width: '40%',
      },
      code: {
        title: 'Kodu',
        width: '20%',
      },
      institutionId: {
        title: 'Kurum',
        width: '40%',
        navigationproperty: 'Institution.Name',
        propType: 'institution',
      },
    };
    if (this.actionRenderer) {
      columns.action = {
        title: 'lem',
        width: '20%',
        type: 'custom',
        filter: false,
        sort: false,
        renderComponent: this.actionRenderer,
      };
    }
    return columns;
  }

  getLabelKey = () => 'name';
  getValueKey = () => 'id';
}
