import { Guid } from 'guid-typescript';
import { IDemand, IListable, IDemandNote, ISmartTableColumns } from '../../interfaces';

export class Demand implements IDemand, IListable {
  id: Guid;
  subject: string;
  status: number;
  assignedPool: number;
  ownerIdentityNumber: string;
  demandInstitutionIds: Array<Guid>;
  workingBy: Guid;
  demandNote: Array<IDemandNote>;

  constructor(
    public demandInstitutionIdsRenderer?: any,
    public demandStatusRenderer?: any,
    public demandStatusFilterer?: any,
    public demandUnitRenderer?: any,
    public demandUnitFilterer?: any,
    public demandNoteRenderer?: any,
  ) {}

  getColumns(): ISmartTableColumns {
    return {
      ownerIdentityNumber: {
        title: 'Bavuru Sahibi',
        width: '10%',
      },
      demandNumber: {
        title: 'Bavuru Numaras',
        width: '10%',
      },
      subject: {
        title: 'Konu',
        width: '30%',
      },
      demandInstitutionIds: {
        title: 'lgili Kurum',
        width: '20%',
        type: 'custom',
        propType: 'institution',
        renderComponent: this.demandInstitutionIdsRenderer,
      },
      status: {
        title: 'Durum',
        width: '10%',
        type: 'custom',
        renderComponent: this.demandStatusRenderer,
        filter: {
          type: 'custom',
          component: this.demandStatusFilterer,
        },
      },
      assignedPool: {
        title: 'lgili Birim',
        width: '10%',
        type: 'custom',
        renderComponent: this.demandUnitRenderer,
        filter: {
          type: 'custom',
          component: this.demandUnitFilterer,
        },
      },
      notes: {
        title: 'Notlar',
        width: '10%',
        filter: false,
        type: 'custom',
        renderComponent: this.demandNoteRenderer,
      },
    };
  }

  getLabelKey = () => 'ownerIdentity';
  getValueKey = () => 'id';
}
