import { IIntegration, IListable, ISmartTableColumns } from '../../interfaces';
import { Guid } from 'guid-typescript';

export class Integration implements IIntegration, IListable {
  id: Guid;
  name: string;
  description: string;
  apiDocument?: string;
  disabled: boolean;
  integrationFormId: Guid;
  institutionId: Guid;
  retryDurationMinutes: number;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      name: {
        title: 'sim',
        width: '30%',
      },
      description: {
        title: 'Aklama',
        width: '40%',
      },
      institutionId: {
        title: 'lgili Kurum',
        width: '20%',
        navigationproperty: 'Institution.Name',
        propType: 'institution',
      },
      disabled: {
        title: 'Aktif',
        valuePrepareFunction: (isDisabled: boolean) => !isDisabled,
        width: '10%',
        propType: 'boolean',
      },
    };
  }

  getLabelKey = () => 'name';

  getValueKey = () => 'id';
}
