import { Guid } from 'guid-typescript';
import { IGuidId } from '../guid-id';

export interface IDemand extends IGuidId {
  subject: string;
  status: number;
  assignedPool: number;
  ownerIdentityNumber: string;
  workingBy: Guid;
  demandInstitutionIds: Array<Guid>;
  demandNote: Array<IDemandNote>;
}

export interface IDemandNote extends IGuidId {
  demandId: Guid;
  note: string;
  noteOwnerId: Guid;
  userName: string;
  institutionName: string;
  role: string;
}

export enum DemandStatus {
  Created = 0,
  Examining = 1,
  Solved = 2,
  Closed = 3,
}

export enum DemandPoolType {
  CallCenter = 0,
  Institute = 1,
  Company = 2,
}
