import { ISubRole, IListable, ISmartTableColumns } from '../../interfaces';
import { Guid } from 'guid-typescript';

export class SubRole implements ISubRole, IListable {
  id: Guid;
  name: string;
  constructor();
  constructor(id?: Guid, name?: string) {
    this.id = id;
    this.name = name;
  }

  getColumns = (): ISmartTableColumns => null;
  getLabelKey = () => 'name';
  getValueKey = () => 'id';
}
