import { IGuidId } from '../guid-id';
import { Guid } from 'guid-typescript';

export interface IProcessInstanceTask extends IGuidId {
  stepIndex: number;
  processInstanceId: Guid;
  taskType: TaskType;
  taskDetails: Array<any>;
  name: string;
}

export enum TaskType {
  FORM = 1,
  SUMMARY = 2,
  FILE = 3,
  SIGN = 4,
  PAYMENT = 5,
  CONFIRMATION = 6,
  NOTIFICATION_CONFIRMATION = 7,
  REDIRECT = 8,
  RESUBMIT = 9,
  LOGIN = 10,
  CONTENT = 11,
  SELECT_ADDRESS = 12,
  SERVICE = 102,
  ACQUIRE_DOCUMENT = 106,
}
