import { Guid } from 'guid-typescript';
import { IListable, IMeslekKolu, ISmartTableColumns } from '@psc-clients/psc-models';
import { Type } from '@angular/core';

export class MeslekKolu implements IMeslekKolu, IListable {
  id: Guid;
  tanimi: string;
  kodu: string;
  sektorId: Guid;
  aktif: boolean;
  private sektorRenderer?: Type<any>;

  constructor(sektorRenderer: Type<any>) {
    this.sektorRenderer = sektorRenderer;
  }

  getColumns(): ISmartTableColumns {
    return {
      kodu: {
        title: 'Kodu',
        width: '10%',
      },
      tanimi: {
        title: 'Tanm',
        width: '60%',
      },
      sektorId: {
        title: 'Sektr',
        type: 'custom',
        renderComponent: this.sektorRenderer,
        width: '20%',
        navigationproperty: 'sektor.tanimi',
      },
      aktif: {
        title: 'Aktif',
        propType: 'boolean',
        width: '10%',
      },
    };
  }

  getLabelKey = () => 'tanimi';
  getValueKey = () => 'id';
}
