import { IGuidId } from '../guid-id';
import { IProcessInstanceTask } from './process-instance-task';
import { Guid } from 'guid-typescript';

export interface IProcessInstance extends IGuidId {
  processInstanceId: Guid;
  processDefName?: string;
  startedAt: Date;
  endedAt: Date;
  deletedAt: Date;
  deleteReason: string;
  state: ProcessStates;
  processTasks: Array<IProcessInstanceTask>;
  processData?: Array<any>;
  processDefKey?: string;
  taskDetails: Array<{
    processTaskId: Guid;
    name: string;
    value: Guid;
  }>;
  staredByName: string;
}

export enum ProcessStates {
  ACTIVE = 0,
  SUSPENDED,
  COMPLETED,
  EXTERNALLY_TERMINATED,
  INTERNALLY_TERMINATED,
}
