import { Guid } from 'guid-typescript';
import { IListable, ISmartTableColumns } from '../../interfaces';
import { ISicilTuru } from '../../interfaces/sys-man/sicil-turu';

export class SicilTuru implements ISicilTuru, IListable {
  id: Guid;
  name: string;
  informationId: Guid;

  getColumns(): ISmartTableColumns {
    return {
      name: {
        title: 'Ad',
        width: '70%',
      },
      informationId: {
        title: 'Bilgi',
        width: '30%',
        type: 'custom',
        propType: 'information',
        navigationproperty: 'Information.Name',
        sort: false,
      },
    };
  }

  getLabelKey = () => 'name';
  getValueKey = () => 'id';
}
