import React from 'react';
import './inputParams.scss';

interface Props {
  paramObj: { [key: string]: string };
}

export class InputParams extends React.Component<Props> {
  render() {
    const keys = Object.keys(this.props.paramObj);
    return (
      <div className="inputparams-wrapper">
        <h1> Gelen Parametreler:</h1>
        <div className="params">
          {keys && keys.length ? (
            keys.map((key, index) => {
              const value = this.props.paramObj[key];
              return (
                <React.Fragment key={index}>
                  <span>
                    {key.toString()}:<b>{value.toString()}</b>
                  </span>
                </React.Fragment>
              );
            })
          ) : (
            <div>parametre bulunamad</div>
          )}
        </div>
      </div>
    );
  }
}
