import { Guid } from 'guid-typescript';
import { IListable, IDemandPool, ISmartTableColumns } from '../../interfaces';

export class DemandPool implements IDemandPool, IListable {
  id: Guid;
  name: string;

  getColumns(): ISmartTableColumns {
    return {
      name: {
        title: 'Talep Havuzunun smi',
        width: '90%',
      },
    };
  }

  getValueKey = () => 'id';
  getLabelKey = () => 'name';
}
