import { IFaaliyetImages, IListable, ISmartTableColumns } from '../../interfaces';
import { Guid } from 'guid-typescript';

export class Swiper implements IFaaliyetImages, IListable {
  id: Guid;
  logoId: string;
  title: string;
  active: boolean;

  getLabelKey = () => 'title';
  getValueKey = () => 'id';

  getColumns(): ISmartTableColumns {
    return {
      title: {
        title: 'Aklama',
        width: '90%',
      },
      active: {
        title: 'Aktif',
        width: '10%',
        propType: 'boolean',
      },
    };
  }
}
