import { Guid } from 'guid-typescript';
import { IAcikRiza, IListable, ISmartTableColumns } from '../../interfaces';

export class AcikRiza implements IAcikRiza, IListable {
  id: Guid;
  tanimi: string;
  content: string;
  active: boolean;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      tanimi: {
        title: 'Tanm',
        width: '90%',
      },
      active: {
        title: 'Aktif',
        propType: 'boolean',
        width: '10%',
      },
    };
  }

  getLabelKey = () => 'tanimi';
  getValueKey = () => 'id';
}
