import { Guid } from 'guid-typescript';
import { IContactInstitution, IListable, ISmartTableColumns } from '../../interfaces';

export class ContactInstitution implements IContactInstitution, IListable {
  id: Guid;
  name: string;

  constructor() {}

  getColumns = (): ISmartTableColumns => {
    return {
      name: {
        title: 'Ad',
        width: '100%',
      },
    };
  };

  getLabelKey = () => 'name';
  getValueKey = () => 'id';
}
