import { Guid } from 'guid-typescript';
import { IPage, IListable, ISmartTableColumns } from '../../interfaces';

export class Page implements IPage, IListable {
  id: Guid;
  title: string;
  url: string;
  content: string;
  active: boolean;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      title: {
        title: 'Balk',
        width: '80%',
      },
      url: {
        title: 'Url',
        width: '10%',
      },
      active: {
        title: 'Aktif',
        propType: 'boolean',
        width: '10%',
      },
    };
  }

  getLabelKey = () => 'title';
  getValueKey = () => 'id';
}
