import { IGuidId } from '../guid-id';
import { Guid } from 'guid-typescript';
import { IDetailedRegister } from './register';

export interface IComponent extends IGuidId {
  formId: Guid;
  key: string;
  label: string;
  placeholder: string;
  helperText: string;
  institutionId: Guid;
  institutionName: string;
  multiple: boolean;
  type: ComponentType;
  textFormat: TextFormat | ContentTextFormat;
  groupId: Guid;
  options: Array<IOption>;
  sequenceNumber: number;
  width: number;
  hidden: boolean;

  optionDescriptionField: string;
  optionProviderType: OptionProviderType;
  optionProviderKey: string;
  optionSourceKey: string;
  optionKeyField: string;
  optionDisplayFeatures: Array<IOptionDisplayFeature>;
  optionDefaultFilters: Array<IOptionFilter>;
  validations: Array<IValidation>;
  dependencies: Array<IComponentDependency>;
  featureOverrides: Array<IFeatureOverride>;

  dependencyTargets?: Array<string>;
}

export enum ContentTextFormat {
  NONE,
  WARNING,
  ERROR,
  SUCCESS,
  INFO,
}

export enum DependencyOperator {
  EQUALS = '=',
  NOT_EQUALS = '!=',
  IN = 'in',
  NOT_NULL = 'nn',
}

export interface IFeatureOverride {
  feature: string;
  overridenComponentKey: string;
  overridenFeature: string;
}

export interface IDependencyFilter extends IVisibilityCondition {
  featureKey: string;
  featureValue: string;
  featureOp: DependencyOperator;
}

export interface IVisibilityCondition {
  dependsOnFeatureKey: string;
  dependsOnFeatureValue: string;
  dependsOnOp: DependencyOperator;
}

export interface IOption {
  Text: string;
  Value: string;
  Description?: string;
  InformationId?: Guid;
}

export interface IComponentDependency {
  dependsOnKey: string; // bakas
  optionFilters: Array<IDependencyFilter>;
  visibilityConditions: Array<IVisibilityCondition>;
  lookups: Array<ILookup>;
}

export interface ILookup {
  lookupComponentKey: string;
  lookupFeatureKey: string;
  valueFeatureKey: string;
}

export interface IComponentFeature {
  component: IComponent;
  isDynamic: boolean;
  register: IDetailedRegister;
}

export interface IValidation {
  validationType: ValidationType;
  value: string;
  message: string;
}

export interface IOptionDisplayFeature {
  key: string;
  title: string;
  size: number;
  order: number;
  prefix: string;
  description?: string;
}

export interface IOptionFilter {
  featureKey: string;
  featureValue: string;
  dependsOnFeatureKey: string;
  dependsOnFeatureValue: string;
  dependsOnOp: string;
  featureOp: string;
}

export enum ValidationType {
  REQUIRED,
  MIN,
  MAX,
  MIN_LENGTH,
  MAX_LENGTH,
  PATTERN,
}

export enum ComponentType {
  TEXT,
  TEXTAREA,
  RADIO,
  CHECKBOX,
  DROPDOWN,
  GRID,
  FILE,
  GROUP,
  CONTENT,
  MAP,
  HIDDEN,
}

export enum TextFormat {
  NONE,
  TEXT,
  NUMERIC,
  DATE,
  EMAIL,
  PASSWORD,
  URL,
}

export enum OptionProviderType {
  NONE,
  SYSTEM_PROVIDED,
  REST,
}

export const dependencyOperatorOptions = [
  { label: 'Eittir', value: DependencyOperator.EQUALS },
  { label: 'Eit Deildir', value: DependencyOperator.NOT_EQUALS },
  { label: 'Bo Deil', value: DependencyOperator.NOT_NULL },
  { label: 'eren', value: DependencyOperator.IN },
];
