import { Guid } from 'guid-typescript';
import {
  BasvuruDurumTuru,
  IBasvuru,
  IBasvuruStatusHistory,
  IBusiness,
  IListable,
  IProcessInstance,
  IProcessType,
  ISmartTableColumns,
  IUser,
} from '../../interfaces';

export class Basvuru implements IBasvuru, IListable {
  id: Guid;
  businessId?: Guid;
  userId: Guid;
  processTypeId: Guid;
  processInstanceId: Guid;
  correlationId: Guid;
  startedAt: Date;
  processType?: IProcessType;
  processInstance: IProcessInstance;
  statusHistory: Array<IBasvuruStatusHistory>;
  business: IBusiness;
  user: IUser;
  statusType: BasvuruDurumTuru;
  statusDescription: string;

  constructor(
    private statusTypeRenderer,
    private statusTypeFilterer,
    private processInstanceRenderer,
    private processTypeRenderer,
    private processDefinitionRenderer,
  ) {}

  getColumns(): ISmartTableColumns {
    return {
      processTypeId: {
        title: 'Bavuru Tr',
        width: '10%',
        type: 'custom',
        renderComponent: this.processTypeRenderer,
        navigationproperty: 'Processtype.Name',
      },
      processDefinition: {
        title: 'Bavuru Konusu',
        width: '20%',
        type: 'custom',
        renderComponent: this.processDefinitionRenderer,
        navigationproperty: 'Processinstance.Processdef.Name',
      },

      startedAt: {
        title: 'Bavuru Tarihi',
        width: '10%',
        propType: 'date',
      },

      statusType: {
        title: 'Durumu',
        width: '10%',
        type: 'custom',
        renderComponent: this.statusTypeRenderer,
        filter: {
          type: 'custom',
          component: this.statusTypeFilterer,
        },
      },

      statusDescription: {
        title: 'Aklama',
        width: '20%',
      },

      processInstance: {
        title: 'lem',
        width: '20%',
        type: 'custom',
        filter: false,
        renderComponent: this.processInstanceRenderer,
      },
    };
  }

  getLabelKey = () => 'id';

  getValueKey = () => 'id';
}
