import { Guid } from 'guid-typescript';
import { IAnnouncement, IListable, ISmartTableColumns } from '../../interfaces';

export class Announcement implements IAnnouncement, IListable {
  id: Guid;
  title: string;
  description: string;
  url?: string;
  publicationDate: Date;
  expirationDate: Date;
  active: boolean;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      title: {
        title: 'Balk',
        width: '50%',
      },
      url: {
        title: 'Balant',
        width: '20%',
      },
      publicationDate: {
        title: 'Yaynlanma Tarihi',
        propType: 'date',
        width: '10%',
      },
      expirationDate: {
        title: 'Son Geerlilik Tarihi',
        propType: 'date',
        width: '10%',
      },
      active: {
        title: 'Aktif',
        width: '10%',
        propType: 'boolean',
      },
    };
  }

  getLabelKey = () => 'title';
  getValueKey = () => 'id';
}
