import React from 'react';

import './app.scss';

import { Route } from 'react-router-dom';
import { Guncelle } from './guncelle/guncelle';
import { Tamamla } from './tamamla/tamamla';
import { InputParams } from './input-params/inputParams';
import { Nav } from './nav';

export class App extends React.Component {
  paramsObj: { [key: string]: string } = {};
  render() {
    const search = window.location.search;
    const params = new URLSearchParams(search);
    for (const value of (params as any).keys()) {
      this.paramsObj[value] = params.get(value);
    }
    const { basvuruId } = this.paramsObj;
    if (!basvuruId) {
      alert('lem yapabilmek iin bavuru Numaras zorunludur.');
      return <div></div>;
    }
    return (
      <div className="app container">
        <Nav></Nav>
        <InputParams paramObj={this.paramsObj}></InputParams>
        <Route path="/" exact component={() => <Tamamla basvuruId={basvuruId}></Tamamla>} />
        <Route
          path="/guncelle"
          exact
          component={() => <Guncelle onSubmit={(event) => this.guncelle(event)}></Guncelle>}
        />
      </div>
    );
  }

  guncelle = (event) => {
    const { basvuruId } = this.paramsObj;
    if (!basvuruId) {
      alert('Bavuru numaras gelen parametreler arasnda yok');
      return;
    }
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(event),
    };
    fetch(
      `https://perbis-test.ticaret.gov.tr/api/workflow/external/basvuru/${basvuruId}/update`,
      requestOptions,
    ).then((r) => alert('Bavuru durumu baaryla gncellenmitir.'));
  };
}

export default App;
