import { Guid } from 'guid-typescript';
import { ISmartTableColumns, IListable, IProcuration, IUser } from '../../interfaces';

export class Procuration implements IProcuration, IListable {
  id: Guid;
  institutionId: Guid;
  roleId: Guid;
  assigneeUserIdentityNumber: Guid;
  assigneeUser: IUser;
  startDate: Date;
  endDate: Date;

  getLabelKey = () => '';
  getValueKey = () => 'id';

  getColumns(): ISmartTableColumns {
    return {
      assigneeUserName: {
        title: 'Ad',
        valuePrepareFunction: (value, rowValue: IProcuration) => rowValue.assigneeUser.name,
        navigationproperty: 'assigneeUser.name',
        width: '20%',
      },
      targetSurname: {
        title: 'Soyad',
        valuePrepareFunction: (value, rowValue: IProcuration) => rowValue.assigneeUser.surname,
        navigationproperty: 'assigneeUser.surname',
        width: '20%',
      },
      institutionId: {
        title: 'Vekalet Verilen Kurum',
        navigationproperty: 'institution.name',
        type: 'custom',
        propType: 'institution',
        width: '40%',
      },
      startDate: {
        title: 'Balang Tarihi',
        propType: 'date',
        width: '10%',
      },
      endDate: {
        title: 'Biti Tarihi',
        propType: 'date',
        width: '10%',
      },
    };
  }
}
