import { IPermission, IListable, ISmartTableColumns } from '../../interfaces';
import { Guid } from 'guid-typescript';

export class Permission implements IPermission, IListable {
  id: Guid;
  module: string;
  group: string;
  name: string;
  description: string;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      name: {
        title: 'Ad',
        width: '30%',
      },
      description: {
        title: 'Aklama',
        width: '20%',
      },
      module: {
        title: 'Modl',
        width: '30%',
      },
      group: {
        title: 'Grup',
        width: '20%',
      },
    };
  }

  getLabelKey = (): string => 'description';
  getValueKey = (): string => 'id';
}
