import { IListable, ISmartTableColumns, IRecordRegister } from '../../interfaces';
import { Guid } from 'guid-typescript';
import { Type } from '@angular/core';

export class RecordRegister implements IRecordRegister, IListable {
  id: Guid;
  name: string;
  recordIds: Array<Guid>;

  constructor(private actionRenderer: Type<any>) {}

  getLabelKey = () => '';
  getValueKey = () => '';
  getColumns = (): ISmartTableColumns => {
    return {
      name: {
        title: 'Ad',
        width: '90%',
      },
      actions: {
        title: 'Kaytlar',
        width: '10%',
        type: 'custom',
        renderComponent: this.actionRenderer,
      },
    };
  };
}
