import { Guid } from 'guid-typescript';

import { Type } from '@angular/core';
import { IListable, IRuhsatTuru, ISmartTableColumns } from '../../interfaces';

export class RuhsatTuru implements IRuhsatTuru, IListable {
  id: Guid;
  informationId: Guid;
  tanimi: string;
  aktif: boolean;

  getColumns(): ISmartTableColumns {
    return {
      tanimi: {
        title: 'Tanm',
        width: '60%',
      },
      informationId: {
        navigationproperty: 'Information.Name',
        title: 'Bilgi',
        width: '30%',
        type: 'custom',
        propType: 'information',
      },
      aktif: {
        title: 'Aktif',
        propType: 'boolean',
        width: '10%',
      },
    };
  }

  getLabelKey = () => 'tanimi';
  getValueKey = () => 'id';
}
