import { IRole, IListable, IPermission, ISubRole, ISmartTableColumns } from '../../interfaces';
import { Guid } from 'guid-typescript';

export class Role implements IRole, IListable {
  id: Guid;
  name: string;
  permissions: Array<IPermission>;
  subRoles: Array<ISubRole>;
  enabled: boolean;

  constructor();
  constructor(
    id?: Guid,
    name?: string,
    permissions?: Array<IPermission>,
    subRoles?: Array<ISubRole>,
    enabled?: boolean,
  ) {
    this.id = id;
    this.name = name;
    this.permissions = permissions || [];
    this.subRoles = subRoles || [];
    this.enabled = enabled;
  }

  getLabelKey = () => 'name';
  getValueKey = () => 'id';
  getColumns(): ISmartTableColumns {
    return {
      name: {
        title: 'Ad',
        width: '100%',
      },
    };
  }
}
