import {
  IUser,
  IListable,
  IUserInstitutionRole,
  ISmartTableColumns,
  IInstitution,
} from '../../interfaces';
import { Guid } from 'guid-typescript';

export class User implements IUser, IListable {
  id: Guid;
  userInstitutionRoles?: Array<IUserInstitutionRole>;
  identityNumber: string;
  name: string;
  surname: string;
  enabled: boolean;
  institutionNames?: Array<string>;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      identityNumber: {
        title: 'Tc Kimlik Numaras',
        width: '20%',
      },
      name: {
        title: 'Ad',
        width: '15%',
      },
      surname: {
        title: 'Soyad',
        width: '15%',
      },
      institutionNames: {
        title: 'Kurum',
        width: '40%',
        type: 'custom',
        propType: 'array',
        navigationproperty: 'Institution.Name',
      },
      enabled: {
        title: 'Aktif',
        width: '10%',
        propType: 'boolean',
      },
    };
  }

  getLabelKey = () => 'identityNumber';
  getValueKey = () => 'id';
}
