import { Guid } from 'guid-typescript';
import { IListable, ISektor, ISmartTableColumns } from '../../interfaces';

export class Sektor implements ISektor, IListable {
  id: Guid;
  tanimi: string;
  kodu: string;
  aktif: boolean;

  constructor() {}

  getColumns(): ISmartTableColumns {
    return {
      kodu: {
        title: 'Kodu',
        width: '10%',
      },
      tanimi: {
        title: 'Tanm',
        width: '80%',
      },
      aktif: {
        title: 'Aktif',
        propType: 'boolean',
        width: '10%',
      },
    };
  }

  getLabelKey = () => 'tanimi';
  getValueKey = () => 'id';
}
