import { Guid } from 'guid-typescript';
import { IGuidId } from '../guid-id';

export interface IProcessDefinition extends IGuidId {
  name: string;
  description: string;
  aktif: boolean;
  processTypeId: Guid;
  processTypeName: string;
  resource?: string;
  notifyOldVersionOn: Date;
  processKey?: string;
  transientable: boolean;
  publishedOn: Date;
  version: number;
  isEditable?: boolean;
  isSubProcess: boolean;
  initializer: ProcessInitializer;
  initializerInstitutions: Array<{ institutionId: Guid }>;
}

export enum ProcessInitializer {
  SYSTEM,
  BUSINESS,
  INSTITUTION,
}

export const processInitializerMapping = [
  { label: 'Sistem', value: ProcessInitializer.SYSTEM },
  { label: 'Kurum', value: ProcessInitializer.INSTITUTION },
  { label: 'Giriimci', value: ProcessInitializer.BUSINESS },
];
