import { Guid } from 'guid-typescript';
import { IListable, ISmartTableColumns } from '../../interfaces';
import { IOzelIzin } from '../../interfaces/sys-man/ozel-izin';

export class OzelIzin implements IOzelIzin, IListable {
  id: Guid;
  name: string;
  description: string;
  institutionId: Guid;
  informationId: Guid;
  active: boolean;

  getColumns(): ISmartTableColumns {
    return {
      name: {
        title: 'Ad',
        width: '10%',
      },
      description: {
        title: 'Tanm',
        width: '20%',
      },
      informationId: {
        navigationproperty: 'Information.Name',
        title: 'Bilgi',
        width: '30%',
        type: 'custom',
        propType: 'information',
      },
      institutionId: {
        title: 'Kurum',
        width: '30%',
        type: 'custom',
        navigationproperty: 'Institution.Name',
        propType: 'institution',
      },
      active: {
        title: 'Aktif',
        width: '10%',
        propType: 'boolean',
      },
    };
  }

  getLabelKey = () => 'name';
  getValueKey = () => 'id';
}
