import React from 'react';
import { BasvuruDurumTuru } from '@psc-clients/psc-models';

const BasvuruDurumTuruOptions: Array<{
  label: string;
  value: BasvuruDurumTuru;
}> = [
  {
    label: 'lem Bekliyor',
    value: BasvuruDurumTuru.WAITING,
  },
  {
    label: 'ptal Edildi',
    value: BasvuruDurumTuru.DENIED,
  },
  {
    label: 'Tamamland',
    value: BasvuruDurumTuru.COMPLETED,
  },
  {
    label: 'Kullanc ptal Etti',
    value: BasvuruDurumTuru.CANCELED,
  },
];

export interface IProps {
  onSubmit: ({ statusType, description }) => void;
}

export function Guncelle(props: IProps) {
  const [basvuruDurumu, setBasvuruDurumu] = React.useState(BasvuruDurumTuru.WAITING);
  const [description, setDescription] = React.useState('');

  return (
    <React.Fragment>
      <div className="form-group">
        <label htmlFor="basvuruDurumu" className="label col-form-label">
          Bavuru Durumu
        </label>
        <select
          value={basvuruDurumu}
          onChange={(event) => setBasvuruDurumu(event.target.value as any)}
          className="form-control"
        >
          <option></option>
          {BasvuruDurumTuruOptions.filter((x) => x.value !== BasvuruDurumTuru.COMPLETED).map(
            (option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ),
          )}
        </select>
      </div>
      <div className="form-group">
        <label htmlFor="description" className="label col-form-label">
          Aklama
        </label>
        <input
          className="form-control"
          type="text"
          value={description}
          onChange={(event) => setDescription(event.target.value)}
          id="description"
        />
      </div>
      <div className="d-flex justify-content-end mx-2">
        <button
          disabled={!(basvuruDurumu !== null && basvuruDurumu !== undefined) || !description}
          onClick={() => props.onSubmit({ statusType: basvuruDurumu, description })}
          type="button"
          className="btn btn-primary"
        >
          Kaydet
        </button>
      </div>
    </React.Fragment>
  );
}
